/* eslint-disable jsx-a11y/label-has-associated-control */
import { Checkbox, Number, Text, useCorn } from '@react-corn/core';
import { useState } from 'react';
export default {
  title: '@react-corn/core',
  parameters: {
    options: {
      showPanel: true
    }
  }
};
export const BasicUsage = () => {
  const [item, setItem] = useState({
    name: 'Foo',
    value: 42,
    ok: false
  });
  const corn = useCorn({
    item,
    onSubmit: newItem => {
      const accepted = window.confirm(`You submitted "${newItem.name}" and ${newItem.value} with ok ${newItem.ok ? '✓' : '✗'}`);
      accepted && setItem(newItem);
    }
  });
  return <form {...corn.form}>
      <Text required {...corn.field('name')} />
      <Number required {...corn.field('value')} />
      <label>
        <Checkbox {...corn.field('ok')} />
        ok
      </label>
      &nbsp;
      <button disabled={!corn.modified}>Submit</button>
      <button type="button" disabled={!corn.modified} onClick={corn.onReset}>
        Reset
      </button>
    </form>;
};
BasicUsage.parameters = {
  ...BasicUsage.parameters,
  docs: {
    ...BasicUsage.parameters?.docs,
    source: {
      originalSource: "() => {\n  const [item, setItem] = useState({\n    name: 'Foo',\n    value: 42,\n    ok: false\n  });\n  const corn = useCorn({\n    item,\n    onSubmit: newItem => {\n      const accepted = window.confirm(`You submitted \"${newItem.name}\" and ${newItem.value} with ok ${newItem.ok ? '\u2713' : '\u2717'}`);\n      accepted && setItem(newItem);\n    }\n  });\n  return <form {...corn.form}>\n      <Text required {...corn.field('name')} />\n      <Number required {...corn.field('value')} />\n      <label>\n        <Checkbox {...corn.field('ok')} />\n        ok\n      </label>\n      &nbsp;\n      <button disabled={!corn.modified}>Submit</button>\n      <button type=\"button\" disabled={!corn.modified} onClick={corn.onReset}>\n        Reset\n      </button>\n    </form>;\n}",
      ...BasicUsage.parameters?.docs?.source
    }
  }
};